import { useContext, useEffect, useRef, useState } from "react"
import "../style/accueil.css"
import { ThemeContext } from "../component/contexts/ThemeContext"



function Accueil(){

  const [state, setState] = useState(0)

  return <>
  <Navigation state={state} setState={setState} /> 
  {state === 0 ? <Presentation setState={setState}  /> : <About />}
  </>
}

function Navigation({state, setState, navRef}){

  const {theme} = useContext(ThemeContext)

  const ACTIVE = {
    light: {
      backgroundColor: "#00AF0A",
      color: "white"
    },
    dark: {
      backgroundColor: "rgb(0, 43, 117)",
      color: "white"
    }
  }

  const INACTIVE = {
    light: {
      backgroundColor: "white",
      color: "black"
    },
    dark: {
      backgroundColor: "rgb(0, 26, 70)",
      color: "white"
    }
    
  }

  const active = theme === "light" ? ACTIVE.light : ACTIVE.dark
  const inactive = theme === "light" ? INACTIVE.light : INACTIVE.dark



  return <div className="accueil_navigation" ref={navRef}>

    <button id={0} 
            style={state === 0 ? active : inactive}
            onClick={() => setState(()=>0)}
    >Prsentation</button>

    <button id={1}
            style={state === 1 ? active : inactive}
            onClick={() => setState(()=>1)}
    >A propos du site</button>

  </div>
}

function Presentation ({setState}){

  const whoRef = useRef()
  const howRef = useRef()
  const whenRef = useRef()

  const {theme} = useContext(ThemeContext)

  const observerWizzard = (e) => {
    e.forEach(elem => {
      if(elem.isIntersecting){
        elem.target.style.opacity = 1
      }
    })
  }

  useEffect(()=>{
    const obs = new IntersectionObserver(observerWizzard)

    if(whoRef.current != undefined){
      obs.observe(whoRef.current)
    }
    if(howRef.current != undefined){
      obs.observe(howRef.current)
    }
    if(whenRef.current != undefined){
      obs.observe(whenRef.current)
    }

    return () =>{
      obs.disconnect()
    }
  }, [])

  return <>
  <div className={`bloc presentation bloc_${theme}`} style={{marginTop: 3.5 + "rem"}} ref={whoRef}>
    <h2>Qui suis-je ?</h2>
    <p>
    Je m'appelle Albin Poutrain, je suis dveloppeur web Front-end. <a href='#' onClick={()=> setState(()=>1)}>Ce site portfolio</a> est dvelopp avec ReactJS.
    </p>

  </div>
  <div className={`bloc presentation bloc_${theme}`} ref={howRef}>
    <h2>Comment ai-je appris ?</h2>
    <p>
      Je suis autodidacte, j'ai donc, en toute autonomie, appris grce  internet par l'intermdiaire de vidos,
      de recherches Google afin de trouver une solution  un problme donn, et bien videmment par la pratique.
      Un cheminement classique d'apprentissage se prsente ainsi:
    </p>
    <ul>
        <li>Acquisition des base (d'un langage, library, framework)</li>
        <li>Mise en pratique des comptences apprise en dveloppant de petits projets</li>
        <li>Ces projets soulvent bien souvent de nouveaux problmes qu'il faut apprendre  rsoudre</li>
        <li>
          Ces problmes se rsolvent d'abord par la rflexion et, au cas chant, par l'aide d'informations trouves
          sur internet donc soit grce aux documentations, soit sur des forums dans le cas o quelqu'un a dj t confront
           ce problme, soit grce  des vidos, soit grce  TchatGPT.
          Dans tous les cas, la solution doit tre comprise avant de l'utiliser.
        </li>
      </ul>

    <p>
          D'abord HTML et CSS puis Javacript pendant une longue periode afin de savoir l'utiliser convenablement. J'ai galement touch
          au Back-end avec NodeJS et fastify afin principalement de comprendre le fonctionnement d'une requte avec fetch, donc le
          fonctionnement d'un GET, d'un POST notamment. Tout ceci afin de comprendre un minimum le fonctionnement global d'un site web.
          J'ai galement permi  mon serveur d'tre accessible au monde en ouvrant le port 80 et en redirigeant le trafic sur mon serveur.
          Cela m'a permi de comprendre le fonctionnement d'internet au global, requte, dns... J'avais utilis Freenom pour avoir un nom de
          domaine gratuit.
          J'ai ensuite appris ReactJS, le principe de composant me plat beaucoup, cela permet d'avoir une application bien organise 
          dont l'affichage est gr dynamiquement directement avec la library et son systme d'tat.
          J'avais galement "connect" la partie Front-end en React avec mon Back-end NodeJS grce au proxy afin de faire quelque requtes
          avec fetch.
      </p> 

  </div>
  <div className={`bloc presentation bloc_${theme}`} ref={whenRef}>
    <h2>Comment tout cela a commenc ?</h2>
    <p>
      J'ai cris ma premire ligne de code  l'age de 11 ans. A l'poque je voulais dvelopper des jeux vidos, type RPG
      alors je me suis mis a apprendre le C++. Cela ma permi de comprendre assez tt le fonctionnement d'un algorithme, et bien
      entendu de me familiariser au code ( c'tait quand mme dj du C++ ). <br></br>
      J'ai finalement arrt puisque je savais que je ne pourrai pas atteindre mon objectif seul. Dvelopper un RPG en 3D 
      m'aurait demand d'avoir des comptences bien trop varies, que ce soit en code ou en modlisation 3D (pour ne citer que a),
      en plus de requrir normment de temps.<br></br><br></br>
      C'est finalement  la fin du lyce pour un projet technique que j'ai de nouveau re touch au code. Pour le cot dveloppement
      web avec du dveloppement Back-end en utilisant php, Front-end uniquement HTML et CSS mais aussi logiciel en utilisant du C++.
      J'ai remarqu ne pas avoir perdu ma capacit de code, et passer d'un langage a un autre n'tait pas si compliqu.
      Aprs le lyce, recherchant un peu de que je voulais faire, j'ai finalement dcid de continuer dans le dveloppement web Front-end
      et d'apprendre par moi-mme.<br></br>

    </p>

  </div>
  </>
}

function About(){

  

  const oneRef = useRef()
  const twoRef = useRef()
  const treeRef = useRef()

  const {theme} = useContext(ThemeContext)

  const observerWizzard = (e) => {
    e.forEach(elem => {
      if(elem.isIntersecting){
        elem.target.style.opacity = 1
      }
    })
  }

  useEffect(()=>{
    const obs = new IntersectionObserver(observerWizzard)
    if(oneRef.current != undefined){
      obs.observe(oneRef.current)
    }
    if(twoRef.current != undefined){
      obs.observe(twoRef.current)
    }
    if(treeRef.current != undefined){
      obs.observe(treeRef.current)
    }

    return () =>{
      obs.disconnect()
    }

  }, [])

  
  return <>
  <div className={`bloc presentation bloc_${theme}`} style={{marginTop: 3.5 + "rem"}} ref={oneRef}>
    <h2>A quoi sert ce site</h2>
    <p>
      Il s'agit d'un site portfolio. Ce site a donc pour objectif de montrer des
      comptence en dveloppement web front-end. 
      Je me suis donc uniquement servi de l'hbergement GitHub puisque je n'ai pas
      dvelopp pour ce site de partie back-end.
    </p>
  </div>

  <div className={`bloc presentation bloc_${theme}`} ref={twoRef}>
    <div >
        
      <h2>Le calcul boursier</h2>
      <p>
        Il m'est moi mme rgulirement utile, il s'agit d'une petite application
        qui a pour but d'effectuer un calcul d'intrt simple mais aussi compos.
      </p>

    </div>
    <div >
      <h2>Le programme sportif</h2>
      <p>
        Il s'agit d'un essai d'application qui a pour objectif de gnrer un programme
        sportif (plutot ax musculation), ainsi qu'une indication des besoin nutritionnels
        journaliers en fonction du poids, de la taille, de l'ge, du taux de sdentarit,
        de l'objectif ainsi que de la morphologie.
        Je concidre qu'il s'agit d'un essai puisque construire un programme qui correspond
        prcisment  chaque personne est bien plus complexe.
        Bien entendu, l'application est fonctionnelle et traite correctement les donnes
        des utilisateurs.
        Concernant l'aspect technique, avoir une base de donne contenant les informations
        sur les diffrents exercices, programmes et sur les dites aurait t bien plus
        convenable. Il aurait alors suffit de faire des requtes au serveur 
        (avec fetch par exemple) pour rcuprer les donnes utiles uniquement. Mon site,
        ne se concentrant que sur l'aspect front, ne comporte donc pas cet aspect.
      </p>
    </div>
    <div className="tree">
      <h2>Le jeu</h2>
      <p>
        J'ai eu l'ide de crer un petit jeu en premier lieu pour le plaisir, j'avais
        une ide que j'avais envie de raliser. Cela dit, l'intrt, avant le divertissement
        ici est de mettre en oeuvre des interractions plus ou moins complexes entre diffrents
        composant tout en faisant en sorte que a fonctionne.
      </p>  

    </div>
      
  </div>
    
  <div className={`bloc presentation bloc_${theme}`} ref={treeRef}>
    <h2>Le style gnral du site</h2>
    <p>
      Je me suis inspir de la plateforme d'investissement en crypto Uphold 
      (attention, pas le site) ainsi que du site web de <a href="https://www.grafikart.fr" target="_blank">Grafikart</a>
    </p>  
  </div>

  </>
}
  
export default Accueil