import { useEffect, useRef, useState } from "react"



export function Menu({onPlaying, playerHp, onRestart}){

    

    const [state, setState] = useState(0)

    const msg = playerHp <= 0 ? "Mort" : "Menu"


    if(state === 1){
        return <div className="modal_menu" >
            <Goal setState={setState}/>
        </div>
    }

    if(state === 2){
        return <div className="modal_menu" >
            <Tip setState={setState}/>
        </div>
    }

    if(state === 3){
        return <div className="modal_menu" >
            <History setState={setState}/>
        </div>
    }



    return <div className="modal_menu" >
        <MainMenu onPlaying={onPlaying} onRestart={onRestart} msg={msg} setState={setState}/>     
    </div>
}

function MainMenu({onPlaying, onRestart, msg, setState}){
    return <div className="menu">
            <h2>{msg}</h2>
            <button onClick={() => onPlaying(true)} >Lancer</button>
            <button onClick={onRestart}>Recommencer</button>
            <button onClick={() => setState(3)}>Histoire</button>
            <button onClick={() => setState(1)}>Objectif</button>
            <button onClick={() => setState(2)}>Tips</button>
        </div>
}

function Goal({setState}){
    return <div className="menu">
        
            <h2>Objectif</h2>
            <p>
                Vous devez voluer jusqu' tre capable de combattre Carrux
                afin de sauver la prairie d'une fin attroce.
            </p>
            <button onClick={()=> setState(0)}>Retour</button>
        </div>
}

function Tip({setState}){
    return <div className="menu">
        
            <h2>Tips</h2>
           <ul>
                <li>
                    Le carr rouge s'appelle Rubixor.
                </li>
                <li>
                    Mettez sur pause, grce au boutton situ en haut  gauche, pour
                    acheter vos amliorations. 
                </li>
                <li>
                    Mourir vous fait perdre de l'argent.
                </li>
                <li>
                    Rubixor volue  chaque fois que vous le tuez.
                </li>
           </ul>
                
            
            <button onClick={()=> setState(0)}>Retour</button>
        </div>
}

function History({setState}){

    const titleRef = useRef()
    const buttonRef = useRef()
    const textRef = useRef()
    const menuRef = useRef()

    const resize = () => {
        const title = titleRef.current.clientHeight
        const button = buttonRef.current.clientHeight

        const textSize = (window.innerHeight/2) - (title + button)
      
        textRef.current.style.height = textSize + "px"
    }

    useEffect(()=>{
        resize()

        window.addEventListener("resize", resize)

        return () => window.removeEventListener("resize", resize)
    }, [])

    console.log()

    return <div className="menu" >
        
            <h2 ref={titleRef}>Histoire</h2>
            <p style={{overflowY: "scroll"}} ref={textRef}>
            Il tait une fois, au cur de la paisible Prairie d'clatiris, 
            un lieu magique o les fleurs clataient de couleurs vives et les papillons 
            jouaient  cache-cache parmi les rayons du soleil. 
            Tout tait en parfaite harmonie jusqu' ce qu'un jour, 
            un mchant carr rouge du nom de Rubixor apparaisse, 
            menaant de dtruire la beaut de la prairie avec sa forme gomtrique sinistre.
            <br></br> <br></br>
             la tte de cette menace carre se trouvait un gorille redoutable appel Carrux, 
            le chef carr rouge et le cerveau derrire le plan visant  transformer la Prairie
            d'clatiris en une terre carre sans vie ni couleur.
            <br></br> <br></br>
            Les habitants de la Prairie d'clatiris, des cratures adorables et joyeuses, 
            taient pris de panique. Les lapins cessaient leurs jeux bondissants, 
            les oiseaux suspendaient leur mlodie enchante, 
            et mme les nuages semblaient perdre leur douceur. 
            La prairie tait plonge dans une atmosphre carrment sombre.
            <br></br> <br></br>
            Cependant, parmi les habitants, se trouvait un petit escargot dtermin 
            du nom de Brillanto. Connu pour sa lenteur mais aussi pour son intelligence, 
            Brillanto dcida de rassembler une quipe improbable pour sauver 
            la Prairie d'clatiris.
            <br></br> <br></br>
            Brillanto recruta un groupe htroclite comprenant un cureuil acrobate 
            du nom de Volti, une libellule experte en voltige arienne appele Ailumi, 
            et un hrisson stratge nomm Piquart. 
            Ensemble, ils laborrent un plan brillant pour contrer les intentions malfiques 
            de Carrux et de son arme de carrs rouges.
            <br></br> <br></br>
            Le jour fatidique arriva. La bataille entre les formes gomtriques et les 
            cratures de la prairie commena. Les acrobaties de Volti dconcertrent 
            les carrs rouges, les voltiges d'Ailumi brouillrent leur vision avec des 
            clats de lumire, et les stratgies de Piquart les repoussrent.
            <br></br> <br></br>
            Finalement, Brillanto affronta le gorille Carrux dans un duel intellectuel. 
            Aprs un change pique de penses stratgiques, Brillanto russit  convaincre 
            Carrux de la beaut et de la diversit de la Prairie d'clatiris, 
            le persuadant d'abandonner son plan diabolique.
            <br></br> <br></br>
            Carrux, touch par la sagesse de Brillanto, ordonna  ses troupes de se retirer. 
            La Prairie d'clatiris retrouva sa splendeur, 
            les habitants reprirent leurs activits joyeuses, 
            et mme Rubixor devint une forme bienveillante, 
            dsireux de participer  la vie harmonieuse de la prairie.
            <br></br> <br></br>
            Et ainsi, la Prairie d'clatiris retrouva sa tranquillit, 
            grce  l'intelligence,  la dtermination et  la sagesse d'un petit escargot 
            nomm Brillanto. 
            La leon de cette histoire improbable tait que mme dans les prairies les plus 
            pacifiques, il pouvait y avoir des menaces carrment tranges, 
            mais avec un esprit d'quipe et de la perspicacit, 
            mme les mchants carrs pouvaient changer pour le mieux.
            
            </p>
            <button onClick={()=> setState(0)} ref={buttonRef}>Retour</button>
        </div>
}

